/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React, { FC, ReactNode } from "react"

import "./layout.css"
import { Anchor, Box, Footer, Text, Grommet, ResponsiveContext } from "grommet"
import { customTheme } from "./theme"
import { Header } from "./header"

const pad = (size: string): { horizontal?: string } | undefined =>
  size === "xxsmall" || size === "xsmall" || size === "small"
    ? undefined
    : {
        horizontal: "xlarge",
      }

const Layout: FC = ({ children }) => (
  <Grommet theme={customTheme} full>
    <ResponsiveContext.Consumer>
      {(size): ReactNode => (
        <Box pad={pad(size)}>
          <Header />
          <Box as="main" pad="medium" flex overflow="auto">
            {children}
          </Box>
          <Footer background="light-4" justify="center" pad="xsmall">
            <Text textAlign="center" size="small">
              © {new Date().getFullYear()}, Built with
              {` `}
              <Anchor href="https://www.gatsbyjs.org">Gatsby</Anchor>
              {` and `}
              <Anchor href="https://v2.grommet.io">Grommet</Anchor>
            </Text>
          </Footer>
        </Box>
      )}
    </ResponsiveContext.Consumer>
  </Grommet>
)

export default Layout
